/**
 * 
 * @param formats(Y-m-d, Y-m-d H:i:s, Ymd, Ymd Hi)
 * @param timestamp 
 * */
export function timestampToYear(timestamp, formats, add) {
  if (!timestamp) return "";
  formats = formats || "Y-m-d";
  let zero = function (value) {
    if (value < 10) {
      return "0" + value;
    }
    return value;
  };
  let myDate;
  if (timestamp && timestamp.toString().length === 10) {
    timestamp = parseInt(timestamp);
    myDate = new Date(timestamp * 1000);
  } else {
    timestamp = parseInt(timestamp);
    myDate = new Date(timestamp);
  }
  let year = myDate.getFullYear();
  let month = zero(myDate.getMonth() + 1);
  let day;
  if (add && add == "day1") {
    day = zero(myDate.getDate() + 1);
  } else {
    day = zero(myDate.getDate());
  }
  // let day = zero(myDate.getDate());
  let hour = zero(myDate.getHours());
  let minite = zero(myDate.getMinutes());
  let second = zero(myDate.getSeconds());
  return formats.replace(/Y|m|d|H|i|s/gi, function (matches) {
    return {
      Y: year,
      m: month,
      d: day,
      H: hour,
      i: minite,
      s: second,
    }[matches];
  });
}

