<script>
import indexHeader from "@/components/header.vue";
import indexTitle from "@/components/title.vue";
import item from "@/components/item.vue";
import foot from "@/components/foot.vue";
import { showConfirmDialog } from "vant";
import { showToast,showSuccessToast,showFailToast } from 'vant';
import { getData } from "../api";
import { getUrlString } from "@/utils/tools.js";
export default {
  components: {
    indexHeader,
    indexTitle,
    item,
    foot,
  },
  data() {
    return {
      env: window.env,
      showZX:false,
      show: false,
      name: "",
      password: "",
      curr: -1,
      active: -1,
      logoAct: -1,
      checked: false,
      email:'',
      namer:'',
      company:'',
      things: [
        {
          text:'',
          title:''
        },
        {
          text:'',
          title:''
        },
        {
          text:'<br/>',
          title:''
        },
        {
          text:'',
          title:''
        },
        {
          text:'<br/>',
          title:''
        }
      ],
      types: [
        {
          name: "AKG",
          active: false,
        },
      ],
      logos: [
        {
          url: require("../assets/img/general/akg-logo.png"),
          active: false,
        },
      ],
      openid: "",
      clubsecret:''
    };
  },
  created() {
    this.openid = getUrlString("openid");
    this.clubsecret = getUrlString("secret")
    this.initData();
    // this.clubsecret = localStorage.getItem('clubsecret');
    // this.showZX = this.clubsecret?true:false;
    if(this.openid && !this.clubsecret){
      this.showZX = false
    } else  if(!this.openid && this.clubsecret){
      this.showZX = true
    }
  },
  methods: {
    initData() {
      const param = {
        action: "getClubUser",
        openid: this.openid,
        secret:this.clubsecret
      };
      getData(param).then((res) => {
        if (res.code == 0) {
          this.logos = res.brand.map((item) => {
            return {
              ...item,
              logo: env.imgUrl + item.logo,
              active: false,
            };
          });
          this.types = res.appCaseTypeList.map((item) => {
            return {
              ...item,
              active: false,
            };
          });
        } else {
          showToast(res.msg);
        }
      });
    },
    getRole(index) {
      this.curr = index;
    },
    getPlace(index) {
      // this.active = index;
      this.types[index].active = !this.types[index].active;
    },
    setOpacity(index) {
      // this.logoAct = index;

      this.logos[index].active = !this.logos[index].active;
    },
    clearInfo() {
      showConfirmDialog({
        // title: '',
        message: "",
      })
        .then(() => {
          // on confirm
          //localStorage.removeItem('clubsecret');
          this.delClubUser();
        })
        .catch(() => {
          // on cancel
        });
    },
    delClubUser(){
      const param = {
        action:'delClubUser',
        secret:this.clubsecret
      }
      getData(param).then(res => {
        if(res.code == 0){
          showToast('');
          this.$router.push({
            name:'index',
          })
        } else {
          showToast(res.msg)
        }
      })
    },

    submit() {
      if(this.curr<0){
        return  showToast('')
      }

      const identity = this.things[this.curr].title;
      
      const logos = this.logos.filter(item => {
        return item.active
      })
      const brandArr = logos.map(item => item.secret);
      const brand= brandArr.join(',')
      if(!brandArr.length){
        return  showToast('')
      }

      //types
      const types = this.types.filter(item => {
        return item.active
      })
      const typesArr = types.map(item => item.secret);
      const scene = typesArr.join(',')
      if(!typesArr.length){
        return  showToast('')
      }
      if(!/^[a-zA-Z0-9.!#$%&'*+\/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/.test(this.email)){
        return showToast('')
      }
      if(!this.namer.trim().length){
        return  showToast('')
      }
      if(!this.company.trim().length){
        return  showToast('')
      }
      console.log('scene--->', scene);
      
      const param = {
        action:'saveClubUser',
        openid:this.openid,
        brand,
        scene,
        email:this.email,
        namer:this.namer,
        company:this.company,
        identity
      }
      getData(param).then(res => {
        if(res.code == 0){
          const secret = res.userData.secret
          showToast('')
          this.$router.push({
            name:'home',
            query:{
              secret
            }
          })
          localStorage.setItem('clubsecret',secret);
          // localStorage.setItem('openid',res.userData.openid);
        } else {
          showToast(res.msg)
        }
      })
      
    },
  },
};
</script>

